nav {
  padding: 1rem 25%;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  background: var(--text-color);
}

nav a {
  text-decoration: none;
  text-transform: uppercase;
  color: var(--background-color);
}

#image {
  width: 100%;
  height: 300px;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: background-image 0.7s ease-in-out;
  background-image: var(--image);
}

nav a:hover {
  color: rgb(12, 216, 189);
}

h1 {
  text-align: center;
  margin: 3rem 0 2rem 0;
}

p {
  width: 40%;
  margin: 1rem auto;
  line-height: 1.6;
}
.advance-search {
  color: #a7afff;
  list-style: none;
  text-decoration: none;
  font-family: Raleway !important;
  letter-spacing: 0.6px;
  font-weight: bold;
}
.advance-search:hover {
  color: #ffffff;
  list-style: none;
  text-decoration: none;
  font-family: Raleway !important;
  letter-spacing: 0.6px;
  font-weight: bold;
}
.dpdown {
  font-size: 50px;
}
.tippy-tooltip {
  font-size: 0.9rem;
  padding: 0.3rem 0.6rem;
}

#darkMode {
  background: transparent url(/static/media/Darkmode.77043999.svg) no-repeat center;
  width: 50px;
  height: 22px;
  border: none;
  transition: 0.3s ease-in-out;
}

#darkMode:hover,
#darkMode:focus {
  -webkit-filter: none;
          filter: none;
  cursor: pointer;
}

#darkMode.clicked {
  -webkit-filter: none;
          filter: none;
  background: transparent url("/static/media/light mode.6afe2aba.svg") no-repeat center;
}

